@tailwind base;
@tailwind components;
@tailwind utilities;

.App {
  padding: 0;
  margin: 0;
  display: flex;
  width: 100vw;
  height: 100vh;
  flex-direction: column;

}

h1{
  font-size: 2rem;
  font-weight: bold;
}

h3{
  font-size: 1.5rem;
  font-weight: bold;
}


.Navtop {
  background-color: #0054a6;
  height: 50px;
  display: flex;
  align-items: center;
  padding: 0 10px 0 20px;
}

.Navtop h3 {
  color: white;
}

.Navtop img {
  height: 30px;
  margin-right: 10px;
}

.Contents {
  padding: 0;
  margin: 0;
  width: 100%;
  height: calc(100vh - 50px);
  display: flex;
  flex-direction: row;
}

.Leftbar {
  background-color: #e7ebef;
  width: 190px;

  padding: 0;
}

.Leftbar ul {
  list-style: none;
  padding: 0;
  /* margin: 10 0 0 0; */
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.Leftbar ul li {
  padding: 0;
  height: 40px;

}

.Leftbar ul li a {
  text-decoration: none;
  background-color: #4295e8;
  color: white;
  padding-left: 10px;
  width: calc(100%);
  height: 40px;
  display: flex;
  align-items: center;
  font-weight: bold;
  gap: 10px;

}

.Leftbar ul li a.active {
  background-color: white;
  color: #0054a6;
}


.Leftbar ul li a i {
  font-size: 1.4rem;
}

.Leftbar ul li a:hover {
  background-color: #0054a6;
  color: white;

}

.maincontent {
  width: 100%;
  /* background-color: rgba(255, 255, 255, 1); */
  padding: 10px;
  height: calc(100vh - 80px);
  overflow-y: scroll;
  color: #0095ff;
}


.maincontent::after {
  content: '';
  background-image: url("./Static/Img/Logo_KCC.png");
  background-position: center;
  position: absolute;
  top: 30px;
  right: 0;
  left: 0;
  bottom: 0;
  z-index: -1;
  background-repeat: no-repeat;
  background-size:  auto 80%;
  opacity: 0.12;

}


.hotspot-custom {
  background: red;
  /* Red dot */
  border-radius: 50%;
  /* Circular shape */
  width: 20px;
  height: 20px;

}

.hotspot-custom:hover {
  background: yellow;
  /* Highlight on hover */
  cursor: pointer;
}


.leaflet-container {
  width: calc(100% - 0px); 
  height: 400px;
}

.legend {
   padding: 6px 8px;
   font: 14px Arial, Helvetica, sans-serif;
   background: rgb(255, 255, 255);
   line-height: 24px;
   color: rgb(0,0,0);
   height: 200px;
   position: absolute;
   z-index: 9999;
   bottom: 0;
   right: 0;
   margin: 15px 10px;
}

.legend h4 {
   text-align: center;
   font-size: 16px;
   margin: 2px 12px 8px;
   color: rgb(0,0,0)
}

.Le_span{
  width: 10px;
  height: 10px;
}



/* Table show  */

.styled-table {
    border-collapse: collapse;
    /* margin: 25px 0; */
    font-size: 0.9em;
    font-family: sans-serif;
    width: 100%;
    /* min-width: 600px; */
    /* box-shadow: 0 0 20px rgba(0, 0, 0, 0.15); */
}


.styled-table thead tr {
    background-color: #28a5ff;
    color: #ffffff;
    text-align: center;
}


.styled-table th,
.styled-table td {
    padding: 12px 15px;
}


.styled-table tbody tr {
    border-bottom: 1px solid #dddddd;
    text-align: center;
}

.styled-table tbody tr:hover, .styled-table tbody tr:nth-of-type(even):hover{
  background-color: #70c3ff8e;
}

.styled-table tbody tr:last-of-type {
    border-bottom: 2px solid #28a5ff;
}

.styled-table tbody tr.active-row {
    font-weight: bold;
    color: #009879;
}


.styled-table button{
  border: none;
  cursor: pointer;
  transition: 1s;
}


.styled-table button:hover{
  transform: scale(1.3);
  
}

.btn-style{
  /* style={{border: 'none', background: 'none', padding: '0', cursor: 'pointer'}} */
  border: none;
  background: none;
  padding: 0;
  cursor: pointer;
}

.rowdata label{
  display: flex;
  gap: 10px;
}

.rowdata select, .rowdata input{
  width: 200px;
  background-color: rgb(219 234 254 / var(--tw-bg-opacity, 1))
}

